@import "./../../common/styles/sass/variables";
@import "./../../common/styles/sass/mixins";

.project {
  width: 450px;
  display: flex;
  flex-direction: column;


  .image {
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    .viewBth {
      background-color: $primaryColor;
      padding: 10px 20px;
      position: relative;
      z-index: 1;
      color: $textColor;
    }
  }

  .content {



      .description {
        @include text($TextFontFamily, 12px, 1.8, 400);
        font-weight: 400;
        color: $textColor;
      }
  }
}

.projectTitle {
  @include text($TitleFontFamily, 19px, 1.8, 400);
  letter-spacing: 1px;
  margin: 0 0 5px;

  &:hover{
    color: #b2e9ff;
  }
}





